import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { ModalComponent } from './components/modal/modal.component';
import { HeroFormComponent } from './components/hero-form/hero-form.component';
import { PaginationComponent } from './components/pagination/pagination.component';
import { HeaderComponent } from './components/header/header.component';
import { LoginFormComponent } from './components/login-form/login-form.component';
import { SiderBarComponent } from './components/sider-bar/sider-bar.component';
import { NavTabComponent } from './components/nav-tab/nav-tab.component';
import { MainContentComponent } from './components/main-content/main-content.component';
import { CarouselComponent } from './components/carousel/carousel.component';
import { RecommendComponent } from './pages/recommend/recommend.component';
import { PlaylistComponent } from './pages/playlist/playlist.component';
import { RadioDjComponent } from './pages/radio-dj/radio-dj.component';
import { RankingListComponent } from './pages/ranking-list/ranking-list.component';
import { SingerComponent } from './pages/singer/singer.component';
import { LatestMusicComponent } from './pages/latest-music/latest-music.component';
import { FindMusicComponent } from './pages/find-music/find-music.component';
import { PrivateFmComponent } from './pages/private-fm/private-fm.component';
import { MvComponent } from './pages/mv/mv.component';
import { FriendComponent } from './pages/friend/friend.component';

const appRoutes: Routes = [
  {
    path: 'find',
    children: [
      { path: 'recommend', component: RecommendComponent },
      { path: 'playlist', component: PlaylistComponent },
      { path: 'ranking', component: RadioDjComponent },
      { path: 'radio', component: RankingListComponent },
      { path: 'singer', component: SingerComponent },
      { path: 'latestmusic', component: LatestMusicComponent }
    ]
  },
  {
    path: 'privatefm',
    component: PrivateFmComponent
  },
  {
    path: 'mv',
    component: MvComponent
  },
  {
    path: 'friends',
    component: FriendComponent
  }
];

@NgModule({
  declarations: [
    AppComponent,
    ModalComponent,
    HeroFormComponent,
    PaginationComponent,
    HeaderComponent,
    LoginFormComponent,
    SiderBarComponent,
    NavTabComponent,
    MainContentComponent,
    CarouselComponent,
    RecommendComponent,
    PlaylistComponent,
    RadioDjComponent,
    RankingListComponent,
    SingerComponent,
    LatestMusicComponent,
    FindMusicComponent,
    PrivateFmComponent,
    MvComponent,
    FriendComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule {}



